@import "settings"
.page-title
  font-family: Esqadero, sans-serif
  font-size: 48px
  line-height: 62px
  font-weight: 400
  span
    color: $red-color
.input-style
  color: $input-color
  padding: $input-padding
  border: $input-border
  border-radius: $input-border-radius
  transition: border-color .3s, color .3s
  @include input-placeholder
    color: $input-placeholder-color
    transition: color .3s
  &:focus
    border-color: $input-focus-border-color
    @include input-placeholder
      color: transparent
  &.error
    border-color: $input-error-border-color
.btn-style
  background: $red-gradient
  color: #fff
  display: inline-block
  padding: 25px 50px 22px
  font-size: 16px
  line-height: 20px
  border: none
  &:hover
    background: $red-color
.check-group
  label
    display: flex
    position: relative
    input
      position: absolute
      top: 0
      left: 0
      width: 0
      height: 0
      visibility: hidden
      opacity: 0
      margin: 0 !important
      &:checked + span:after
        opacity: 1
    span
      display: inline-block
      color: rgba(#fff, .5)
      font-size: 10px
      line-height: 17px
      padding-left: 28px
      &:before
        content: ''
        display: block
        border: 1px solid #fff
        width: 18px
        height: 18px
        position: absolute
        top: 7px
        left: 0
      &:after
        content: '\e805'
        font-family: Fontello
        font-style: normal
        font-weight: normal
        speak: never
        display: flex
        align-items: center
        justify-content: center
        margin: 0
        text-decoration: inherit
        text-align: center
        font-variant: normal
        text-transform: none
        color: $red-color
        width: 16px
        height: 16px
        position: absolute
        top: 8px
        left: 1px
        opacity: 0
        font-size: 7px
        line-height: 16px
        transition: opacity .3s
    a
      display: inline-block

.breadcrumbs
  margin: 52px 0
  &__list
    display: flex
    flex-wrap: wrap
    align-items: center
    justify-content: flex-start
  &__item
    display: inline-flex
    align-items: center
    justify-content: flex-start
    flex: 0 0 auto
    width: auto
    position: relative
    &:after
      content: '\e801'
      font-family: Fontello
      font-style: normal
      font-weight: normal
      speak: never
      display: inline-block
      margin: 0
      text-decoration: inherit
      text-align: center
      font-variant: normal
      text-transform: none
      color: $red-color
      font-size: 9px
      line-height: 9px
      margin: 0 16px
    &:last-child
      &:after
        display: none
  &__link
    display: inline-block
    font-size: 14px
    line-height: 20px
    color: rgba($dark-color, .5)
    transition: color .3s
    &:hover
      color: $dark-color
  &__current
    color: $dark-color
    font-size: 14px
    line-height: 20px

.pagination-list
  display: inline-flex
  align-items: center
  justify-content: flex-start
  flex-wrap: wrap
  max-width: 100%
  &__item
    flex: 0 0 auto
    width: auto
  &__link
    display: flex
    align-items: center
    justify-content: center
    min-width: 12px
    margin: 0 9px
    color: $dark-color
    font-size: 16px
    line-height: 24px
    transition: color .3s
    &:hover
      color: $red-color
    &.active
      color: $red-color
      font-weight: 500
      font-size: 24px
      line-height: 24px
    &.prev-link, &.last-link
      width: 41px
      height: 41px
      border: 1px solid rgba($dark-color, .5)
      transition: border-color .3s
      &:hover
        border-color: $red-color
      .icon
        font-size: 11px
        line-height: 11px
        color: $red-color
      &.disabled
        .icon
          color: rgba($dark-color, .5)
    &.prev-link
      margin-left: 0
      margin-right: 15px
      .icon
        transform: scale(-1,1)
    &.last-link
      margin-right: 0
      margin-left: 15px




.product-item
  &.block-view
    padding-bottom: 151px
    position: relative
    border: 1px solid rgba($dark-color, .5)
    .image-block
      .item-link
        display: block
      .item-image
        display: block
        width: auto
        max-width: 100%
        margin: 0
    .descr-block
      padding: 15px 25px
      .item-name
        font-weight: 600
        color: $red-color
        font-size: 16px
        line-height: 27px
      .item-price
        display: block
        margin-top: 20px
        font-weight: 500
        font-size: 16px
        line-height: 20px
      .item-properties
        margin: 12px 0 0 0
        &__item
          display: block
          font-size: 16px
          line-height: 27px
          &:nth-of-type(1)
            margin-top: 0
          span
            font-weight: 500
    .btn-block
      padding: 0 25px 25px
      position: absolute
      bottom: 0
      left: 0
      width: 100%
      .input-group
        &.count-group
          display: flex
          align-items: center
          justify-content: flex-start
          span
            font-size: 16px
            line-height: 22px
            font-weight: 500
            display: inline-block
            margin-right: 24px
          .count-btn
            flex: 0 0 20px
            width: 20px
            height: 20px
            display: flex
            align-items: center
            justify-content: center
            color: $red-color
            font-weight: 500
            font-size: 20px
            line-height: 20px
          .input-count
            flex: 0 0 auto
            width: 100%
            max-width: 50px
            text-align: center
            border: none
            background-color: transparent
            font-weight: 500
        &.btn-group
          margin-top: 30px
          button
            display: block
            width: 100%
            background: $dark-color
            &:hover
              background: $red-color !important
    &:hover
      border-color: $dark-color
      background: linear-gradient(153.61deg, #252525 42.22%, #0D0D0D 89.58%)
      .descr-block
        color: #fff
        .item-name
          color: #fff
      .btn-block
        color: #fff
        .input-group
          &.count-group
            .count-btn
              color: #fff
            .input-count
              color: #fff
          &.btn-group
            button
              background: $red-gradient
  &.list-view
    position: relative
    border: 1px solid rgba($dark-color, .5)
    display: flex
    align-items: stretch
    justify-content: flex-start
    max-height: 188px
    .image-block
      flex: 0 0 186px
      width: 186px
      .item-link
        display: block
      .item-image
        display: block
        width: auto
        max-width: 100%
        margin: 0
    .descr-block
      flex: 0 0 245px
      width: 100%
      padding: 13px 20px
      overflow: hidden
      .item-name
        display: inline-block
        color: $red-color
        font-size: 16px
        line-height: 27px
        font-weight: 500
      .item-properties
        font-size: 14px
        line-height: 14px
        margin-top: 16px
        &__item
          margin-top: 10px
          &:nth-of-type(1)
            margin-top: 0
          span
            font-weight: 500
    .btn-block
      display: flex
      align-items: center
      justify-content: flex-end
      flex: 0 0 calc(100% - 186px - 245px)
      width: auto
      margin: 0 0 0 auto
      padding: 20px
      .addcart-form
        display: flex
        align-items: center
        justify-content: flex-end
        .input-group
          &.count-group
            display: flex
            align-items: center
            justify-content: center
            flex: 0 0 175px
            width: 100%
            max-width: 175px
            margin: 0 auto
            span
              font-size: 16px
              line-height: 22px
              font-weight: 500
              display: inline-block
              margin-right: 24px
            .count-btn
              flex: 0 0 20px
              width: 20px
              height: 20px
              display: flex
              align-items: center
              justify-content: center
              color: $red-color
              font-weight: 500
              font-size: 20px
              line-height: 20px
            .input-count
              flex: 0 0 auto
              width: 100%
              max-width: 50px
              text-align: center
              border: none
              background-color: transparent
              font-weight: 500
          &.price-group
            font-size: 16px
            line-height: 27px
            flex: 0 0 175px
            width: 100%
            max-width: 175px
            text-align: center
            margin: 0 42px 0 0
          &.btn-group
            flex: 0 0 175px
            width: 100%
            max-width: 175px
            button
              background: $dark-color
              &:hover
                background: $red-color

    &:hover
      background: linear-gradient(153.61deg, #252525 42.22%, #0D0D0D 89.58%)
      .descr-block
        .item-name
          color: #fff
        .item-properties
          color: #fff
      .btn-block
        .addcart-form
          .input-group
            &.count-group
              .count-btn
                color: #fff
              .input-count
                color: #fff
            &.price-group
              color: #fff
            &.btn-group
              button
                background: $red-gradient
                &:hover
                  background: $red-color






