@charset "UTF-8";
@import "/node_modules/swiper/swiper.min.css";
.page-title {
  font-family: Esqadero, sans-serif;
  font-size: 48px;
  line-height: 62px;
  font-weight: 400;
}
.page-title span {
  color: #BD2F2F;
}

.input-style {
  color: #000;
  padding: 10px 20px;
  border: 1px solid #1F1F1F;
  border-radius: 0;
  transition: border-color 0.3s, color 0.3s;
}
.input-style.placeholder {
  color: rgba(255, 255, 255, 0.5);
  transition: color 0.3s;
}
.input-style::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
  transition: color 0.3s;
}
.input-style::-moz-placeholder {
  color: rgba(255, 255, 255, 0.5);
  transition: color 0.3s;
}
.input-style:-moz-placeholder {
  color: rgba(255, 255, 255, 0.5);
  transition: color 0.3s;
}
.input-style:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
  transition: color 0.3s;
}
.input-style:focus {
  border-color: #1F1F1F;
}
.input-style:focus.placeholder {
  color: transparent;
}
.input-style:focus::-webkit-input-placeholder {
  color: transparent;
}
.input-style:focus::-moz-placeholder {
  color: transparent;
}
.input-style:focus:-moz-placeholder {
  color: transparent;
}
.input-style:focus:-ms-input-placeholder {
  color: transparent;
}
.input-style.error {
  border-color: #BD2F2F;
}

.btn-style {
  background: linear-gradient(113.71deg, #BD2F2F 14.38%, #FF4848 110.44%);
  color: #fff;
  display: inline-block;
  padding: 25px 50px 22px;
  font-size: 16px;
  line-height: 20px;
  border: none;
}
.btn-style:hover {
  background: #BD2F2F;
}

.check-group label {
  display: flex;
  position: relative;
}
.check-group label input {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  visibility: hidden;
  opacity: 0;
  margin: 0 !important;
}
.check-group label input:checked + span:after {
  opacity: 1;
}
.check-group label span {
  display: inline-block;
  color: rgba(255, 255, 255, 0.5);
  font-size: 10px;
  line-height: 17px;
  padding-left: 28px;
}
.check-group label span:before {
  content: "";
  display: block;
  border: 1px solid #fff;
  width: 18px;
  height: 18px;
  position: absolute;
  top: 7px;
  left: 0;
}
.check-group label span:after {
  content: "\e805";
  font-family: Fontello;
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  text-decoration: inherit;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  color: #BD2F2F;
  width: 16px;
  height: 16px;
  position: absolute;
  top: 8px;
  left: 1px;
  opacity: 0;
  font-size: 7px;
  line-height: 16px;
  transition: opacity 0.3s;
}
.check-group label a {
  display: inline-block;
}

.breadcrumbs {
  margin: 52px 0;
}
.breadcrumbs__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
}
.breadcrumbs__item {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  flex: 0 0 auto;
  width: auto;
  position: relative;
}
.breadcrumbs__item:after {
  content: "\e801";
  font-family: Fontello;
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: inline-block;
  margin: 0;
  text-decoration: inherit;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  color: #BD2F2F;
  font-size: 9px;
  line-height: 9px;
  margin: 0 16px;
}
.breadcrumbs__item:last-child:after {
  display: none;
}
.breadcrumbs__link {
  display: inline-block;
  font-size: 14px;
  line-height: 20px;
  color: rgba(31, 31, 31, 0.5);
  transition: color 0.3s;
}
.breadcrumbs__link:hover {
  color: #1F1F1F;
}
.breadcrumbs__current {
  color: #1F1F1F;
  font-size: 14px;
  line-height: 20px;
}

.pagination-list {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  max-width: 100%;
}
.pagination-list__item {
  flex: 0 0 auto;
  width: auto;
}
.pagination-list__link {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 12px;
  margin: 0 9px;
  color: #1F1F1F;
  font-size: 16px;
  line-height: 24px;
  transition: color 0.3s;
}
.pagination-list__link:hover {
  color: #BD2F2F;
}
.pagination-list__link.active {
  color: #BD2F2F;
  font-weight: 500;
  font-size: 24px;
  line-height: 24px;
}
.pagination-list__link.prev-link, .pagination-list__link.last-link {
  width: 41px;
  height: 41px;
  border: 1px solid rgba(31, 31, 31, 0.5);
  transition: border-color 0.3s;
}
.pagination-list__link.prev-link:hover, .pagination-list__link.last-link:hover {
  border-color: #BD2F2F;
}
.pagination-list__link.prev-link .icon, .pagination-list__link.last-link .icon {
  font-size: 11px;
  line-height: 11px;
  color: #BD2F2F;
}
.pagination-list__link.prev-link.disabled .icon, .pagination-list__link.last-link.disabled .icon {
  color: rgba(31, 31, 31, 0.5);
}
.pagination-list__link.prev-link {
  margin-left: 0;
  margin-right: 15px;
}
.pagination-list__link.prev-link .icon {
  transform: scale(-1, 1);
}
.pagination-list__link.last-link {
  margin-right: 0;
  margin-left: 15px;
}

