.footer
  margin-top: auto
  background: $black-gradient
  padding: 50px 0 0
  &__content
    display: flex
    flex-wrap: wrap
    align-items: stretch
    justify-items: flex-start
    &__main
      flex: 0 0 300px
      max-width: 300px
      .logo-link
        position: relative
        left: -14px
        .logo-image
          display: block
          margin: 0
      .backcall-btn
        margin-top: 22px
      address
        color: #fff
        display: block
        position: relative
        padding-left: 30px
        line-height: 27px
        font-style: normal
        margin-top: 30px
        &:before
          content: '\e80d'
          font-family: Fontello
          font-style: normal
          font-weight: normal
          speak: never
          display: block
          margin: 0
          text-decoration: inherit
          text-align: center
          font-variant: normal
          text-transform: none
          color: $red-color
          position: absolute
          top: calc(50% - 10px)
          left: 0
          font-size: 20px
          line-height: 20px
      time
        color: #fff
        display: block
        position: relative
        padding-left: 30px
        line-height: 25px
        font-style: normal
        margin-top: 21px
        &:before
          content: '\e815'
          font-family: Fontello
          font-style: normal
          font-weight: normal
          speak: never
          display: block
          margin: 0
          text-decoration: inherit
          text-align: center
          font-variant: normal
          text-transform: none
          color: $red-color
          position: absolute
          top: calc(50% - 10px)
          left: 0
          font-size: 18px
          line-height: 20px
      .phone-links
        display: flex
        flex-direction: column
        align-items: flex-start
        justify-items: flex-start
        position: relative
        padding-left: 32px
        margin-top: 28px
        &:before
          content: '\e810'
          font-family: Fontello
          font-style: normal
          font-weight: normal
          speak: never
          display: block
          margin: 0
          text-decoration: inherit
          text-align: center
          font-variant: normal
          text-transform: none
          color: $red-color
          position: absolute
          top: calc(50% - 12px)
          left: 0
          font-size: 18px
          line-height: 20px
        .phone-link
          display: inline-block
          color: #fff
          font-size: 20px
          line-height: 20px
          &:nth-of-type(2)
            margin-top: 18px
      .mail-link
        color: #fff
        display: inline-block
        position: relative
        padding-left: 38px
        line-height: 25px
        font-style: normal
        margin-top: 30px
        &:after
          content: ''
          display: block
          width: calc(100% - 38px)
          height: 1px
          border-bottom: 1px dashed rgba(#fff, .5)
          position: absolute
          bottom: -1px
          right: 0
          transition: border-color .3s
        &:before
          content: '\e80c'
          font-family: Fontello
          font-style: normal
          font-weight: normal
          speak: never
          display: block
          margin: 0
          text-decoration: inherit
          text-align: center
          font-variant: normal
          text-transform: none
          color: $red-color
          position: absolute
          top: calc(50% - 10px)
          left: 0
          font-size: 18px
          line-height: 20px
        &:hover
          &:after
            border-color: #fff

    &__menu
      display: flex
      flex-wrap: wrap
      align-items: stretch
      justify-content: space-between
      flex: 0 0 calc(100% - 300px - 106px)
      max-width: calc(100% - 300px - 106px)
      margin-left: 106px
      .fmenu__column
        flex: 0 0 auto
        max-width: calc(25% - 20px)
        margin: 0 0 40px 0
        padding: 0 2px
        &:nth-of-type(4n)
          margin-left: 35px
        &.about-column
          .menu-list
            margin-top: 7px
            &__link
              color: #fff
              font-weight: 500
              font-size: 20px
            &__item
              margin-top: 42px
              &:nth-of-type(1)
                margin-top: 0
        .menu-title
          display: block
          color: $red-color
          font-weight: 500
          font-size: 20px
          line-height: 20px
          margin-top: 7px
        .menu-list
          margin-top: 36px
          &__item
            display: block
            width: 100%
            margin-top: 27px
            &:nth-of-type(1)
              margin-top: 0
            &.more-link
              margin-top: 32px
              .menu-list__link
                position: relative
                display: inline-flex
                align-items: center
                justify-content: flex-start
                font-size: 16px
                line-height: 20px
                &:after
                  content: '\e80a'
                  font-family: Fontello
                  font-style: normal
                  font-weight: normal
                  speak: never
                  display: inline-block
                  margin: 0 -35px 0 16px
                  text-decoration: inherit
                  text-align: center
                  font-variant: normal
                  text-transform: none
                  font-size: 7px
                  line-height: 10px
                  transition: transform .3s
                  color: $red-color
                &:hover
                  &:after
                    transform: translateX(5px)
            &.submenu-item
              position: relative
              .menu-list__link
                position: relative
                padding-right: 25px
                &:after
                  content: '\e80e'
                  font-family: Fontello
                  font-style: normal
                  font-weight: normal
                  speak: never
                  display: block
                  margin: 0
                  text-decoration: inherit
                  text-align: center
                  font-variant: normal
                  text-transform: none
                  position: absolute
                  top: calc(50% - 3px)
                  right: 1px
                  width: 10px
                  height: 10px
                  font-size: 7px
                  line-height: 10px
                  color: $red-color
          &__link
            color: #fff
            display: inline-block
            transition: color .3s
            &:hover
              color: $red-color

  .copyright
    margin-top: 49px
    border-top: 1px solid rgba(#fff, .1)
    .container
      display: flex
      align-items: center
      justify-content: space-between
      flex-wrap: wrap
      padding: 35px 15px
      color: rgba(#fff, .5)
      font-size: 14px
      line-height: 14px
      a
        color: rgba(#fff, .5)
        transition: color .3s
        &:hover
          color: $red-color
          text-decoration: underline
      span
        a
          color: $red-color
          text-decoration: none
          display: inline-block
          margin-left: 5px