@import "settings"
@import "_mixins/_mixins"
@import "ui"

/* main page */
.main-comfort
  padding: 120px 0 136px
  background-color: #F6F6F6
  &__descr
    line-height: 27px
    margin-top: 20px
  &__content
    display: flex
    flex-wrap: wrap
    align-items: stretch
    justify-content: flex-start
    margin-top: 89px
    &__item
      max-width: calc(25% - 23px)
      flex: 0 0 calc(25% - 23px)
      margin: 0 30px 30px 0
      border: 1px solid $red-color
      text-align: center
      &:nth-of-type(4n)
        margin-right: 0
      .icon-block
        display: flex
        align-items: center
        justify-content: center
        flex-wrap: wrap
        background-color: #F6F6F6
        position: relative
        z-index: 1
        width: 100%
        max-width: 160px
        margin: 0 auto
        top: -30px
      .descr-block
        margin-top: -14px
        padding: 0 24px 24px
        .item-title
          display: block
          font-weight: 500
          font-size: 18px
          line-height: 31px
          margin-bottom: 19px
        p
          line-height: 27px

.main-backcall
  &__title
    margin-bottom: 25px
  &__content
    display: flex
    flex-wrap: wrap
    width: 100%
    align-items: stretch
    justify-content: flex-start
    color: #fff
    &__left-side
      flex: 0 0 50%
      max-width: 50%
    &__right-side
      flex: 0 0 50%
      max-width: 50%
      padding: 90px 15px 15px
      p
        margin-bottom: 7px
  &__form
    background-repeat: no-repeat
    background-position: top center
    background-size: contain
    background-image: url(../images/main/main/mobile.png)
    max-width: 420px
    padding: 75px 70px 0 65px
    margin: -45px 75px 0 auto
    .input-group
      margin-top: 32px
      label
        display: inline-block
        color: #fff
        font-weight: 500
        font-size: 20px
        line-height: 20px
      input
        display: block
        margin-top: 10px
        width: 100%
        background-color: transparent
        border: 1px solid rgba(#fff, .5)
        transition: border-color .3s
        color: #fff
        padding: 22px 19px 23px
        @include input-placeholder
          color: rgba(#fff, .5)
        &:focus
          border-color: #fff
      &:nth-of-type(1)
        margin-top: 0
      &.btn-group
        margin-top: 25px
        button
          display: block
          width: 100%
      &.check-group
        margin-top: 16px
        padding-bottom: 24px

.main-news
  padding: 120px 0 90px
  background-color: #F6F6F6
  .title-row
    display: flex
    align-items: center
    justify-content: space-between
    .see-more
      display: inline-flex
      align-items: center
      justify-content: flex-start
      color: $txt-color
      position: relative
      transition: color .3s
      font-weight: 500
      &:after
        content: '\e80a'
        display: inline-block
        font-family: Fontello
        font-style: normal
        font-weight: normal
        speak: never
        margin: 0
        text-decoration: inherit
        text-align: center
        font-variant: normal
        text-transform: none
        font-size: 7px
        line-height: 7px
        color: $red-color
        transition: transform .3s
        margin-left: 14px
      &:hover
        color: $red-color
        &:after
          transform: translateX(5px)
  &__content
    margin-top: 50px
    .swiper-wrapper
      display: flex
      flex-wrap: wrap
      align-items: stretch
      justify-content: flex-start
      .slide-item
        flex: 0 0 calc(25% - (90px / 4))
        max-width: calc(25% - (90px / 4))
        margin: 0 auto 30px 0
        position: relative
        &:nth-of-type(4n)
          margin-right: 0
        .slide-link
          display: block
          width: 100%
          height: 100%
          position: absolute
          top: 0
          left: 0
          z-index: 1
        .image-block
          .slide-image
            display: block
            width: auto
            max-width: 100%
            margin: 0
            filter: grayscale(100%)
            transition: filter .3s
        .descr-block
          position: absolute
          bottom: 0
          left: 0
          width: 100%
          height: 100%
          display: flex
          flex-direction: column
          align-items: flex-start
          justify-content: flex-end
          padding: 27px 25px
          background: linear-gradient(186.94deg, rgba(19, 8, 8, 0) 33.24%, rgba(0, 0, 0, 0.85) 53.71%)
          time
            color: rgba(#fff, .5)
            display: block
            margin-bottom: 14px
            transition: color .3s
          .slide-title
            display: inline-block
            color: #fff
            font-weight: 500
            font-size: 18px
            line-height: 31px
            margin-bottom: 8px
          .slide-descr
            color: #fff
            line-height: 27px
            margin-bottom: 30px
          .slide-more
            display: inline-flex
            align-items: center
            justify-content: flex-start
            color: #fff
            margin-left: 5px
            .icon
              font-size: 7px
              margin-left: 16px
              transition: transform .3s, color .3s
        &:hover
          .image-block
            .slide-image
              filter: grayscale(0)
          .descr-block
            time
              color: #fff
            .slide-more
              .icon
                transform: translateX(5px)
                color: $red-color

.catalog-layout
  padding-bottom: 120px
  &__content
    display: flex
    flex-wrap: wrap
    align-items: flex-start
    justify-content: flex-start
    /* fix for calc */
    .calculate__title
      flex: 0 0 100%
      max-width: 100%
      width: 100%
      margin-bottom: 50px
    &__left-side
      flex: 0 0 328px
      max-width: 328px
      width: 100%
      position: sticky
      top: 20px
      left: 0
      .catalog-aside
        &__menu
          background: $black-gradient
          color: #fff
          padding: 32px 25px 28px
          .aside-menu
            &__title
              font-weight: 500
              font-size: 20px
              line-height: 20px
            &__list
              margin: 24px 0 0 0
            &__item
              display: block
              width: 100%
              margin-top: 22px
              &:nth-of-type(1)
                margin-top: 0
            &__link
              font-size: 16px
              line-height: 20px
              color: #fff
              transition: color .3s
              &:hover
                color: $red-color
          .calc-menu
            &__list
              margin-top: 32px
            &__item
              margin-top: 28px
              .submenu-list
                padding-left: 24px
                margin-top: 20px
                &__item
                  margin-top: 11px
                  &:nth-of-type(1)
                    margin-top: 0
                &__link
                  color: #fff
                  font-size: 16px
                  line-height: 20px
                  transition: color .3s
                  &:hover
                    color: $red-color
              &.active
                .calc-menu__link
                  color: $red-color
                  text-shadow: 0px 0px 25px rgba(255, 255, 255, 0.15)
            &__link
              color: #fff
              font-size: 20px
              line-height: 20px
              font-weight: 500
              transition: color .3s
              &:hover
                color: $red-color

        &__currency
          margin-top: 35px
          .block-title
            color: rgba($dark-color, .5)
            font-size: 18px
            line-height: 20px
            padding-bottom: 15px
            border-bottom: 1px solid rgba($dark-color, .5)
            span
              color: $dark-color
              font-weight: 500
              display: inline-block
              margin-right: 10px
          .block-list
            margin: 25px 0 0 0
            &__item
              display: flex
              align-items: center
              justify-content: flex-start
              font-size: 16px
              line-height: 20px
              margin-top: 18px
              &:nth-of-type(1)
                margin-top: 0
              .icon
                display: inline-block
                margin-right: 20px
              span
                font-weight: 500
                display: inline-block
                margin-right: 3px
        &__metall
          margin-top: 40px
          margin-bottom: 38px
          .block-title
            color: rgba($dark-color, .5)
            font-size: 18px
            line-height: 20px
            padding-bottom: 13px
            border-bottom: 1px solid rgba($dark-color, .5)
            span
              color: $dark-color
              font-weight: 500
              display: inline-block
              margin-right: 10px
          .block-list
            margin-top: 13px
            font-size: 16px
            line-height: 20px
            &__item
              display: flex
              align-items: center
              justify-content: space-between
              border-bottom: 1px dashed rgba($dark-color, .5)
              margin-bottom: 32px
              span
                background-color: $bg-color
                display: inline-block
                margin-bottom: -5px
                &.metall
                  font-weight: 500
              &.item-title
                font-size: 14px
                line-height: 20px
                border-bottom: none
                margin-bottom: 20px
      .calc-btn
        display: flex
        align-items: center
        justify-content: center
        width: 100%
        padding: 23px 10px
        .icon
          display: inline-block
          font-size: 24px
          margin-right: 10px
          position: relative
          top: 1px

    &__right-side
      flex: 0 0 calc(100% - 328px - 30px)
      max-width: calc(100% - 328px - 30px)
      width: 100%
      margin-left: 30px
      .catalog-menu
        margin-top: 30px
        margin-bottom: 90px
        & + .catalog-filter
          margin-top: -31px
        &__list
          display: flex
          flex-wrap: wrap
          align-items: stretch
          justify-content: flex-start
        &__item
          flex: 0 0 calc(33.3% - 20px)
          max-width: calc(33.3% - 20px)
          margin: 0 auto 30px 0
          &:nth-of-type(3n)
            margin-right: 0
        &__link
          display: flex
          align-items: center
          justify-content: center
          flex-direction: column
          flex-wrap: wrap
          border: 1px solid $red-color
          transition: background-color .3s, color .3s
          color: $dark-color
          padding: 20px 20px 26px
          span
            font-weight: 500
            font-size: 20px
            line-height: 20px
            margin-top: 16px
          &:hover
            color: #fff
            background-color: $red-color
      .catalog-filter
        &__type
          margin-top: 40px
          &:nth-of-type(1)
            margin-top: 0
          .type-title
            font-weight: 500
            font-size: 24px
            line-height: 24px
          .input-row
            display: flex
            flex-wrap: wrap
            align-items: stretch
            justify-content: flex-start
            margin-top: 22px
            .input-group
              flex: 0 0 auto
              width: auto
              margin: 0 20px 20px 0
              label
                position: relative
                input
                  position: absolute
                  top: 0
                  left: 0
                  width: 0
                  height: 0
                  opacity: 0
                  visibility: hidden
                  &:checked + span
                    background-color: $red-color
                    color: #fff
                span
                  display: flex
                  align-items: center
                  justify-content: center
                  flex-wrap: wrap
                  height: 47px
                  padding: 0 14px
                  color: $dark-color
                  font-weight: 500
                  font-size: 16px
                  line-height: 20px
                  border: 1px solid $red-color
                  transition: background-color .3s, color .3s
                &:hover
                  span
                    color: #fff
                    background-color: $red-color
      .single-info
        display: flex
        flex-wrap: wrap
        align-items: stretch
        justify-content: flex-start
        margin-top: 30px
        .image-block
          flex: 0 0 443px
          max-width: 443px
          width: 100%
          .product-image
            display: block
            width: auto
            margin: 0
            max-width: 100%
        .descr-block
          flex: 0 0 calc(100% - 443px - 30px)
          max-width: calc(100% - 443px - 30px)
          margin-left: 30px
          &__item
            display: flex
            align-items: center
            justify-content: flex-start
            margin-top: 28px
            &:nth-of-type(1)
              margin-top: 24px
            .item-icon
              display: flex
              align-items: center
              justify-content: center
              flex: 0 0 60px
              width: 60px
              .icon
                display: block
                width: auto
                max-width: 100%
                margin: 0 auto
            .item-descr
              flex: 0 0 calc(100% - 60px)
              max-width: calc(100% - 60px)
              padding-left: 35px
              h3
                font-weight: 500
                font-size: 18px
                line-height: 30px
              p
                font-size: 16px
                line-height: 27px
        & + .products-list
          margin-top: 117px
      .products-list
        display: flex
        flex-wrap: wrap
        align-items: flex-end
        justify-content: flex-start
        margin-top: 59px
        &__views
          display: flex
          align-items: center
          justify-content: flex-start
          flex: 0 0 auto
          max-width: 50%
          width: auto
          margin: 0
          span
            display: inline-block
            flex: 0 0 auto
            width: auto
            font-weight: 500
            font-size: 24px
            line-height: 41px
            margin-right: 25px
          .view-link
            display: inline-flex
            align-items: center
            justify-content: center
            flex: 0 0 41px
            width: 41px
            height: 41px
            border: 1px solid $red-color
            margin-right: 20px
            color: $dark-color
            transition: color .3s, background-color .3s
            .icon
              font-size: 16px
              &.icon-blocks
                font-size: 18px
            &:hover, &.active
              background-color: $red-color
              color: #fff
          & + .products-list__count
            margin-left: 30px
        &__count
          display: flex
          align-items: center
          justify-content: flex-start
          flex: 0 0 auto
          max-width: 50%
          width: auto
          margin: 0
          span
            display: inline-block
            flex: 0 0 auto
            width: auto
            font-weight: 500
            font-size: 24px
            line-height: 41px
            margin-right: 25px
          .count-link
            display: inline-flex
            align-items: center
            justify-content: center
            flex: 0 0 41px
            width: 41px
            height: 41px
            border: 1px solid $red-color
            margin-right: 20px
            color: $dark-color
            transition: color .3s, background-color .3s
            &:hover, &.active
              background-color: $red-color
              color: #fff

        &__clean
          flex: 0 0 auto
          max-width: 50%
          width: auto
          margin: 0 0 4px  auto
          .clean-filter
            display: inline-block
            color: $red-color
            font-weight: 500
            font-size: 18px
            line-height: 20px
            position: relative
            &:after
              content: ''
              display: block
              width: 100%
              height: 1px
              border-bottom: 1px dashed rgba($dark-color, .5)
              position: absolute
              bottom: -6px
              left: 0
              transition: border-color .3s
            &:hover
              &:after
                border-color: $red-color

        &__subcategory
          flex: 0 0 100%
          max-width: 100%
          width: 100%
          margin-top: 30px
          .subcategory-list
            display: flex
            flex-wrap: wrap
            align-items: stretch
            justify-content: flex-start
            &__item
              flex: 0 0 calc(25% - 23px)
              max-width: calc(25% - 23px)
              margin: 0 30px 30px 0
              &:nth-of-type(4n)
                margin-right: 0
            &__link
              display: flex
              align-items: center
              justify-content: center
              color: $dark-color
              border: 1px solid $red-color
              padding: 23px 10px 22px
              transition: background-color .3s, color .3s
              &:hover
                background-color: $red-color
                color: #fff
          & + .products-list__content
            margin-top: 20px
        &__content
          display: flex
          flex-wrap: wrap
          flex: 0 0 100%
          max-width: 100%
          width: 100%
          margin-top: 50px
          &__item
            flex: 0 0 calc(33.3% - 19px)
            max-width: calc(33.3% - 19px)
            margin: 0 29px 30px 0
            &:nth-of-type(3n)
              margin-right: 0
            &.list-view
              flex: 0 0 100%
              width: 100%
              max-width: 100%
              margin: 0 0 30px 0
          &__table
            width: 100%
            max-width: 100%
            overflow-x: scroll
            @include scrollbars(3px, $scrl-bar_track, $scrl-bar_thumb, $scrl-bar_thumbh, 0)
            padding-bottom: 20px
            .items-table
              width: 100%
              tr
                position: relative
                th
                  border: 1px solid #aaa
                  border-right: none
                  border-bottom: none
                  text-align: center
                  color: $red-color
                  padding: 25px 19px 22px
                  font-size: 14px
                  line-height: 20px
                  font-weight: 400
                  &:nth-of-type(1)
                    text-align: left
                  &:last-child
                    min-width: 68px
                    border: none
                    border-left: 1px solid #aaa
                td
                  text-align: center
                  align-content: center
                  border: 1px solid #aaa
                  border-right: none
                  border-bottom: none
                  &.name-td
                    font-weight: 500
                    font-size: 14px
                    line-height: 20px
                    padding: 0 12px 0 20px
                    text-align: left
                    .item-name
                      color: $dark-color
                  &.diameter-td,
                  &.brand-td,
                  &.length-td,
                  &.price-td
                    font-weight: 500
                    font-size: 14px
                    line-height: 20px
                    padding: 0 20px
                  &.count-td
                    text-align: center
                    .input-group
                      display: inline-flex
                      align-items: center
                      justify-content: flex-start
                      span
                        font-size: 16px
                        line-height: 22px
                        font-weight: 500
                        display: inline-block
                        margin-right: 24px
                      .count-btn
                        flex: 0 0 20px
                        width: 20px
                        height: 20px
                        display: flex
                        align-items: center
                        justify-content: center
                        color: $red-color
                        font-weight: 500
                        font-size: 20px
                        line-height: 20px
                      .input-count
                        flex: 0 0 auto
                        width: 100%
                        max-width: 50px
                        text-align: center
                        border: none
                        background-color: transparent
                        font-weight: 500
                  &.btn-td
                    .addcart-btn
                      display: block
                      color: #fff
                      padding: 24px 0
                      width: 100%
                      flex: 0 0 100%
                      .icon
                        font-size: 20px
                        line-height: 20px
                  &:last-child
                    position: sticky
                    top: 0
                    right: 0
                    display: flex
                    align-items: center
                    justify-content: center
                    background-color: $dark-color
                    color: #fff
                &:last-child
                  td
                    border-bottom: 1px solid #aaa
                &:hover
                  background: $black-gradient
                  th
                    background: $bg-color
                  td
                    color: #fff
                    &.name-td
                      .item-name
                        color: #fff
                    &.count-td
                      .input-group
                        .count-btn
                          color: #fff
                        .input-count
                          color: #fff
                    &.btn-td
                      .addcart-btn
                        background: $red-gradient
                        &:hover
                          background: $red-color
            & + .products-list__content__btn-row
              margin-top: 27px
          &__btn-row
            flex: 0 0 100%
            max-width: 100%
            margin-top: 20px
            text-align: center
            .more-products
              display: block
              max-width: 262px
              margin: 0 auto
              padding: 24px 10px
              text-align: center
            .pagination-list
              margin-top: 24px
      .services-list
        display: flex
        flex-wrap: wrap
        align-items: stretch
        justify-content: flex-start
        margin-top: calc(50px + 66px)
        &__item
          flex: 0 0 calc(33.3% - 20px)
          max-width: calc(33.3% - 20px)
          width: 100%
          margin: 0 30px 116px 0
          position: relative
          border: 1px solid $red-color
          &:nth-of-type(3n)
            margin-right: 0
          .icon-block
            position: absolute
            display: flex
            align-items: center
            justify-content: center
            flex-wrap: wrap
            top: -66px
            left: 25px
            width: 90px
            height: 90px
            background: $black-gradient
            .icon
              display: block
              width: auto
              max-width: 100%
              margin: 0 auto
          .descr-block
            padding: 39px 24px 23px
            min-height: 164px
            h3
              font-weight: 500
              font-size: 18px
              line-height: 25px
            .service-link
              display: inline-block
              font-weight: 500
              font-size: 16px
              line-height: 20px
              color: $dark-color
              margin-top: 28px
              .icon
                display: inline-flex
                align-items: center
                justify-content: flex-start
                font-size: 7px
                line-height: 20px
                transition: transform .3s
                margin-left: 12px
                position: relative
                top: -2px
              &:hover
                .icon
                  transform: translateX(5px)
          &:hover
            background: $red-color
            .descr-block
              color: #fff
              .service-link
                color: #fff
                .icon
                  color: #fff

      .service-single
        &__main
          color: #fff
          padding: 50px 50px 58px
          .service-title
            max-width: 530px
          .service-descr
            max-width: 420px
            margin-top: 20px
            font-size: 16px
            line-height: 27px
          .service-order
            margin-top: 44px
        &__info
          margin-top: 80px
          display: flex
          align-items: center
          justify-content: flex-start
          flex-wrap: wrap
          .image-block
            flex: 0 0 447px
            max-width: 447px
            width: 100%
          .descr-block
            flex: 0 0 calc(100% - 447px)
            max-width: calc(100% - 447px)
            padding: 18px 0 20px 30px
            font-size: 16px
            line-height: 27px
            b
              font-weight: 600
              color: $red-color

        &__addinfo
          margin-top: 75px
          .addinfo-list
            display: flex
            flex-wrap: wrap
            align-items: stretch
            justify-content: flex-start
            margin-top: 85px
            &__item
              flex: 0 0 calc(33.3% - 20px)
              max-width: calc(33.3% - 20px)
              margin: 0 30px 30px 0
              position: relative
              &:nth-of-type(3n)
                margin-right: 0
              .icon-block
                width: 160px
                background-color: $bg-color
                position: absolute
                top: -35px
                left: 0
                right: 0
                margin: 0 auto
                .icon
                  display: block
                  width: auto
                  max-width: 100%
                  margin: 0 auto
              .descr-block
                border: 1px solid $red-color
                padding: 30px 23px 24px
                text-align: center
                height: 100%
                .item-title
                  display: block
                  font-size: 18px
                  line-height: 24px
                  font-weight: 500
                  margin: 20px 0 22px
                p
                  font-size: 16px
                  line-height: 27px
        &__advantages
          margin-top: 90px
          .advantages-descr
            margin-top: 30px
            font-size: 16px
            line-height: 27px
            ol
              margin: 0
              padding: 0 0 0 21px
              text-indent: 3px

        & + .backcall-section
          margin-top: 120px

      .reference
        margin-top: 25px
        .reference-links
          font-size: 18px
          line-height: 24px
          &__item
            display: block
            margin-top: 12px
            &:nth-of-type(1)
              margin-top: 0
          &__link
            display: inline-block
            color: $dark-color
            transition: color .3s
            text-decoration: underline
            position: relative
            &:after
              content: '\e80a'
              font-family: Fontello
              font-style: normal
              font-weight: normal
              speak: never
              display: inline-block
