            height: 4px
            position: absolute
            top: -2px
            left: 58px
            background-color: $bg-color
          .auto-item
            flex: 0 0 auto
            max-width: 122px
            margin: 0 auto 0 0
            .auto-name
              color: $red-color
              font-weight: 500
              font-size: 20px
              line-height: 20px
            .properties-list
              margin: 7px 0 0 0
              font-weight: 500
              font-size: 12px
              line-height: 12px
              &__item
                margin-top: 8px
                &:nth-of-type(1)
                  margin-top: 0
            .auto-image
              margin-top: 25px
              display: block
              width: auto
              max-width: 122px
            &:nth-of-type(5n)
              margin-right: 0
      .pickup-section
        display: flex
        flex-wrap: wrap
        align-items: center
        justify-content: flex-start
        &__title
          flex: 0 0 100%
          width: 100%
          margin-bottom: 50px
        &__map
          flex: 0 0 566px
          max-width: 566px
          width: 100%
        &__descr
          flex: 0 0 calc(100% - 566px)
          max-width: calc(100% - 566px)
          font-size: 16px
          line-height: 27px
          padding: 0 20px 2px 30px
          b
            color: $red-color
            font-weight: 600
          p
            margin-bottom: 27px
            &:last-child
              margin-bottom: 0
      .payment-section
        margin-top: 115px
        &__descr
          margin-top: 10px
          font-size: 16px
          line-height: 22px
          h3
            font-weight: 600
            font-size: 16px
          .payments-list
            margin: 34px 0 0 0
            span
              display: inline-block
              &:nth-of-type(1)
                color: rgba($dark-color, .5)
                flex: 0 0 302px
                width: 302px
                position: relative
              &:nth-of-type(2)
                flex: 1 1 auto
                width: auto
                padding-left: 5px
            &__item
              display: flex
              align-items: baseline
              margin-top: 25px
              &:nth-of-type(1)
                margin-top: 0
          .copy-link
            margin-top: 32px
        & + .backcall-section
          margin-top: 120px
      .backcall-section
        display: flex
        flex-wrap: wrap
        align-items: stretch
        justify-content: flex-start
        color: #fff
        &__subtitle
          font-size: 16px
          line-height: 27px
          margin-top: 10px
        &__form
          flex: 0 0 calc(100% - 566px)
          max-width: calc(100% - 566px)
          width: 100%
          background: $black-gradient
          padding: 35px 30px 35px 35px
          .input-group
            margin-top: 21px
            &:nth-of-type(1)
              margin-top: 26px
            label
              color: #fff
              font-weight: 500
              font-size: 18px
              line-height: 30px
            input[type="text"],input[type="email"], textarea
              display: block
              width: 100%
              border: 1px solid $dark-color
              background-color: #2e2e2e
              margin-top: 5px
              padding: 22px 19px
              @include input-placeholder
                color: rgba(#fff, .5)
            textarea
              min-height: 112px
              padding: 13px 19px
            &.btn-group
              margin-top: 30px
              button
                display: block
                width: 100%
            &.check-group
              margin-top: 15px

        &__image
          flex: 0 0 566px
          max-width: 566px
          width: 100%
          display: block
        & + .news-section
          margin-top: 118px
      .news-section
        &__title-row
          display: flex
          flex-wrap: wrap
          align-items: center
          justify-content: space-between
          .see-more
            display: inline-flex
            align-items: center
            justify-content: flex-start
            color: $txt-color
            position: relative
            transition: color .3s
            font-weight: 500
            &:after
              content: '\e80a'
              display: inline-block
              font-family: Fontello
              font-style: normal
              font-weight: normal
              speak: never
              margin: 0
              text-decoration: inherit
              text-align: center
              font-variant: normal
              text-transform: none
              font-size: 7px
              line-height: 7px
              color: $red-color
              transition: transform .3s
              margin-left: 14px
            &:hover
              color: $red-color
              &:after
                transform: translateX(5px)
        &__content
          margin-top: 52px
          .swiper-wrapper
            display: flex
            flex-wrap: wrap
            align-items: stretch
            justify-content: flex-start
            .slide-item
              flex: 0 0 calc(33.3% - 20px)
              max-width: calc(33.3% - 20px)
              margin: 0 30px 30px 0
              position: relative
              &:nth-of-type(3n)
                margin-right: 0
              .slide-link
                display: block
                width: 100%
                height: 100%
                position: absolute
                top: 0
                left: 0
                z-index: 1
              .image-block
                .slide-image
                  display: block
                  width: auto
                  max-width: 100%
                  margin: 0
                  filter: grayscale(100%)
                  transition: filter .3s
              .descr-block
                position: absolute
                bottom: 0
                left: 0
                width: 100%
                height: 100%
                display: flex
                flex-direction: column
                align-items: flex-start
                justify-content: flex-end
                padding: 27px 25px
                background: linear-gradient(186.94deg, rgba(19, 8, 8, 0) 33.24%, rgba(0, 0, 0, 0.85) 53.71%)
                time
                  color: rgba(#fff, .5)
                  display: block
                  margin-bottom: 14px
                  transition: color .3s
                .slide-title
                  display: inline-block
                  color: #fff
                  font-weight: 500
                  font-size: 18px
                  line-height: 31px
                  margin-bottom: 8px
                .slide-descr
                  color: #fff
                  line-height: 27px
                  margin-bottom: 30px
                .slide-more
                  display: inline-flex
                  align-items: center
                  justify-content: flex-start
                  color: #fff
                  margin-left: 5px
                  .icon
                    font-size: 7px
                    margin-left: 16px
                    transition: transform .3s, color .3s
              &:hover
                .image-block
                  .slide-image
                    filter: grayscale(0)
                .descr-block
                  time
                    color: #fff
                  .slide-more
                    .icon
                      transform: translateX(5px)
                      color: $red-color
        & + .docs-section
          margin-top: 90px
      .docs-section
        &__list
          display: flex
          flex-wrap: wrap
          align-items: stretch
          justify-content: flex-start
        &__item
          flex: 0 0 calc(33.3% - 20px)
          max-width: calc(33.3% - 20px)
          width: 100%
          margin: 30px 30px 0 0
          &:nth-of-type(3n)
            margin-right: 0
          .item-link
            display: flex
            align-items: flex-start
            justify-content: flex-start
            flex-direction: column
            border: 1px solid $red-color
            color: $dark-color
            padding: 30px 24px 32px
            .item-name
              font-size: 18px
              line-height: 22px
              text-decoration: underline
            .item-download
              display: inline-flex
              align-items: center
              justify-content: flex-start
              position: relative
              font-weight: 500
              margin-top: 25px
              &:after
                content: '\e80a'
                font-family: Fontello
                font-style: normal
                font-weight: normal
                speak: never
                display: inline-block
                text-decoration: inherit
                text-align: center
                font-variant: normal
                text-transform: none
                transition: transform .3s
                font-size: 7px
                line-height: 7px
                margin: 0 0 0 14px
              .icon
                margin-right: 20px
                color: $red-color
                font-size: 35px
                line-height: 20px
            &:hover
              background: $red-gradient
              color: #fff
              .item-download
                .icon
                  color: #fff
                &:after
                  transform: translateX(5px)





/* error page */
.error-page
  padding-bottom: 120px
  &__content
    width: 100%
    max-width: 804px
    background-image: url(../images/main/error/error_bg.jpg)
    background-repeat: no-repeat
    background-position: center
    background-size: cover
    color: #fff
    padding: 28px 30px
  .error-code
    display: block
    font-weight: 500
    font-size: 18px
    line-height: 20px
    margin: 12px 0
  &__descr
    font-size: 18px
    line-height: 31px
    max-width: 335px
    margin: 26px 0
    p
      margin-bottom: 32px
      &:last-child
        margin-bottom: 0
  .back-link
    margin: 2px 0


@import "_components/footer"
@import "_components/modals"
@import "/node_modules/swiper/swiper.min.css"
@import "_animates/_animates"